}

.buttonSearch {
  border: 0;
  border: 2px solid tomato;
  border-radius: 15px;
  padding: 10px;
  background-color: rgb(78, 127, 232);
  color: white;
  font-size: 20px;
  font-family: "Raleway", sans-serif;
}

.buttonSearch:hover {
  background-color: rgb(221, 86, 86);
  cursor: pointer;
}
