.ul {
  /* min-height: 70vh; */
  min-width: 250px;
  display: inline-block;
  max-height: 91.5vh;
  overflow-y: scroll;
  list-style-type: none;
  padding: 0 2px 0px 2px;
  background-color: #383e72;
  font-family: "Raleway", sans-serif;
  scrollbar-width: thin;
  scrollbar-color: rgb(78, 127, 232) white;
}

.ul::-webkit-scrollbar {
  width: 12px;
}

.ul::-webkit-scrollbar-track {
  background: white;
}

.ul::-webkit-scrollbar-thumb {
  background-color: rgb(78, 127, 232);
  border-radius: 20px;
}

.liTitle {
  
  display: block;
  color: white;
  background-color: rgb(78, 127, 232);
  padding: 8px 20px;
  font-size: 20px;
  font-weight: bold;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
}

.li {
  display: block;
  color: white;
  padding: 8px 20px;
  /* padding: 10px; */
  text-decoration: none;
}

.li:hover {
  background-color: rgb(105, 128, 128);
  color: white;
  cursor: pointer;
}




  .loader {
    position: fixed;
    top: calc(50% - 40px);
    left: calc(50% - 40px);
    background-color: transparent;

    border: 10px solid #b8b3b3;
    border-top: 10px solid rgb(19, 137, 167);
    border-radius: 50%;
    width: 100px;
    height: 100px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
